/* Dashboard layout */

.dashboard_container {
	width: 100%;
	padding: 0;
	margin: 0;
  }
  
  .client_cont {
	display: flex;
	flex-direction: row;
	flex-wrap: wrap;
	flex: 1 1;
	width: 100%;
	padding: 0;
	margin: 0;
  }
  
  .sidebar_container {
	height:auto;
	width: 27rem;
	transition: -webkit-transform 0.3s ease-in-out;
	transition: transform 0.3s ease-in-out;
	transition: transform 0.3s ease-in-out, -webkit-transform 0.3s ease-in-out;
  }
  
  .sidebar_container.open {
	-webkit-transform: translateX(0);
	        transform: translateX(0);
  }
  
  .client_area {
	position: absolute;
	left: 27rem;
	display: flex;
	height: 100%;
	width: 82%;
	/* margin-top: 20px; */
	background-color: #0b0f18;
	overflow-x:auto;
	padding: 0;
  }
  
  .client_area.mobile {
	margin-top: 60px;
	left: 0;
	position:relative;
	height: 100vh;
	width: 100%;
	z-index:97;
	transition: left 0.3s ease-in-out;
  }
  
  .client_area.mobile.sidebar-open {
	left: 27rem;
  }
  
  .toggle_button {
	display: none; /* Hide toggle button by default */
	position: fixed;
	top: 1rem;
	left: 1rem;
	z-index: 100;
	cursor: pointer;
	color: rgb(0, 0, 0);
	font-size: 25px;
  }
  
  @media (max-width: 500px) {
	.client_area {
	  margin-left: 0;
	}
	
	.toggle_button {
	  display: block; /* Show toggle button on mobile devices */
	}
  }
  
